<template>
	<div
		class="slider"
	>
		<ul class="slider__arrow">
			<li
				class="slider__arrow--left"
				v-if="currentPage !== 0"
				@click="navigatePage('left')"
			></li>

			<li
				class="slider__arrow--right"
				v-if="currentPage !== this.sliders.length - 1"
				@click="navigatePage('right')"
			></li>
		</ul>

		<ul class="slider__inner" :class="`slider__inner-page${currentPage}`">
			<li class="slider__item" v-for="(slider, key) in sliders">
				<img class="slider__img" :src="require(`../../assets/images/sliders/slider-${key}.png`)" alt="">

				<div class="slider__text">
					<h2 class="slider__heading">{{ slider.title }}</h2>
					<span class="slider__desc">{{ slider.desc }}</span>
				</div>
			</li>
		</ul>

		<ul class="slider__pager">
			<li
				v-for="(slider, key) in sliders"
				@click="setActivePager(key)"
				:class="{ active: key === currentPage }"
			></li>
		</ul>
	</div> <!-- // slider -->
</template>

<script>
export default {
  name: 'slider',

	data() {
		return {
			currentPage: 0,

			sliders: [
				{
					title: '1キャッチコピーが入ります。',
					desc: '1サンプルテキストサンプルテキストサンプルテキストサンプルテキスト サンプルテキストサンプルテキストサンプルテキストサンプルテキ'
				},
				{
					title: '2キャッチコピーが入ります。',
					desc: '2サンプルテキストサンプルテキストサンプルテキストサンプルテキスト サンプルテキストサンプルテキストサンプルテキストサンプルテキ'
				},
				{
					title: '3キャッチコピーが入ります。',
					desc: '3サンプルテキストサンプルテキストサンプルテキストサンプルテキスト サンプルテキストサンプルテキストサンプルテキストサンプルテキ'
				}
			]
		}
	},

	methods: {
		setActivePager(pageNo) {
			this.currentPage = pageNo
		},

		navigatePage(direction) {
			if (direction === 'right') {
				this.currentPage < this.sliders.length - 1 ? this.currentPage++ : ''
			} else {
				this.currentPage > 0 ? this.currentPage-- : this.currentPage * 1
			}
		}

	},

	computed: {
		sliderWidth() {

		}
	}
}
</script>



